.skills__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3rem;
}

.skill {
    background: var(--color-bg-variant);
    border-radius: 2rem;
    border: 1px solid var(--color-primary);
    height: fix-content;
    transition: var(--transition);
    justify-content: center;
    max-height: 30rem;
    padding-bottom: 5rem;
    overflow: hidden;
}

.skill__list-div {
    overflow-y: scroll;
    overflow-x: hidden;
    scrollbar-width: none;
    -ms-overflow-style: none;
    max-height: 100%;
}

.skill:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
}

.skill__head {
    background: var(--color-primary);
    padding: 2rem;
    margin: -2px;
    border-radius: 2rem;
    border-shadow: 0 2rem 1rem rgba(0,0,0,0.1);
}

.skill__head h3 {
    color: var(--color-bg);
    font-size: 1rem;
    text-align: center;
}

.skill__list {
    padding: 2rem;
}

.skill__list li {
    display: flex;
    gap: 1rem;
    margin-bottom: 0.8rem;
}

.skill__list-icon {
    color: var(--color-primary);
    margin-top: 2px;
    font-size: 1.2rem;
}

.skill__list p {
    font-size: 0.9rem;
}

.experience__details {
    display: flex;
    align-items: center;
    align-content: center;
    gap: 1rem;
}

.experience__details-icon {
    margin-top: 6px;
    color: var(--color-primary);
}


/* ============= MEDIA QUERIES (MEDIUM DEVICES) ============= */
@media screen and (max-width: 1024px) {
    .skills__container {
        grid-template-columns: 1fr 1fr;
        gap: 2rem;
    }

    .skill {
        height: auto;
    }
}
  
/* ============= MEDIA QUERIES (SMALL DEVICES) ============= */
@media screen and (max-width: 600px) {
    .skills__container {
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }
} 