.experience__swiper {
    overflow-y: none;
    overflow-x: scroll;
    white-space: nowrap;
    gap: 1.5rem;
    padding-bottom: 1rem;
}

.experience__single {
    margin-right: 1.5rem;
    width: 35%;
    display: inline-block;
    border: 1px solid var(--color-primary);
    border-radius: 2rem;
    padding: 1.5rem;
    background-color: var(--color-bg);
}

.full-width {
    width: 100%;
}

.full-width .experience__technologies {
    display: inline-block;
}

.experience__icon {
    font-size: 1.2rem;
    color: var(--color-primary);
    text-align: center;
}

.tech-skill {
    display: inline-block;
    padding: 0.25rem 0.5rem;
}

.experience__technologies {
    display: flex;
    flex-direction: row;
    overflow-x: scroll;
    overflow-y: hidden;
    scrollbar-width: none;
    -ms-overflow-style: none;
    gap: 0.25rem;
    padding: 0.5rem 0;
}

.experience__technologies::-webkit-scrollbar {
    display: none;
}

.exp-title{
    overflow-x: scroll;
    overflow-y: none;
    scrollbar-width: none;
    -ms-overflow-style: none;
}

.exp-company{
    overflow-x: scroll;
    overflow-y: none;
    scrollbar-width: none;
    -ms-overflow-style: none;
}

.history{
    overflow-x: scroll;
    overflow-y: none;
    scrollbar-width: none;
    -ms-overflow-style: none;
}

.description-box-exp {
    white-space: normal;
    /* height : calc(1.2rem * 4);
    display: -webkit-box;
    -webkit-line-clamp: 4; */
    margin: 0;
    height: 10rem;
    /* text-overflow: ellipsis; */
    -webkit-box-orient: vertical;
    overflow: scroll;
    scrollbar-width: none;
    -ms-overflow-style: none;
}

.line-override {
    height: unset !important;
    -webkit-line-clamp: unset !important;
}

.line-override .card-images {
    display: block !important;
}

.mySwiper img {
    max-height: 40vh;
    max-width: 80%;
    margin: 0 auto;
    object-fit: contain;
}

.card-images {
    display: none;
    /* center it with 50% width */
    margin: 0 auto;
    /* set width to max 100% */
    max-width: 100%;
    max-height: 45vh;
    padding: 1rem;
}

.learn-more-div {
    margin: 0 auto;
    text-align: right;
}

.animated {
  overflow: hidden;
  width: 11rem;
  white-space: nowrap;
}

.animated > * {
  display: inline-block;
  position: relative;
  animation: 3s linear 0s infinite alternate move;
}

.animated > *.min {
  min-width: 100%;
}

@keyframes move {
  0%,
  25% {
    transform: translateX(0%);
    left: 0%;
  }
  75%,
  100% {
    transform: translateX(-100%);
    left: 100%;
  }
}

@media only screen and (max-width: 768px) {
    .experience__swiper {
        gap: 1rem;
    }

    .experience__single {
        width: 90%;
        font-size: 0.8rem;
    }
}

@media only screen and (max-width: 480px) {
    .experience__swiper {
        gap: 1rem;
    }

    .experience__single {
        width: 90%;
        font-size: 0.8rem;
    }
}