.portfolio__swiper {
    border: 1px solid red;
    display: flex;
}

.portfolio img {
    border-radius: 2rem;
}

.portfolio__single {
    border: 1px solid var(--color-primary);
    border-radius: 2rem;
    flex: 1;
    padding: 1.5rem;
    overflow: hidden;
    display: flex;
}

.portfolio__title {
    display: flex;
}

.portfolio__desc {
    display: flex;
}

.portfolio__icon {
    font-size: 1.2rem;
    color: var(--color-primary);
    text-align: center;
}

.portfolio__technologies {
    display: flex;
    flex-direction: row;
    overflow-x: scroll;
    overflow-y: hidden;
    scrollbar-width: none;
    -ms-overflow-style: none;
    gap: 0.25rem;
    padding: 0.5rem 0;
}

.portfolio__technologies::-webkit-scrollbar {
    display: none;
}

.description-box {
    display: -webkit-box;
    -webkit-line-clamp: 4;
    margin: 0;
    text-overflow: ellipsis;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

.learn-more-div {
    margin: 0 auto;
    text-align: right;
}

.portfolio__image-container {
    width: 50%;
}

.portfolio__image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 2rem;
}