header {
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* ============= CTA ============= */
.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;

}

/* ============= HEADER SOCIAL ============= */
.header__social {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    right: 0;
    top: 6rem;
}

/* make stylign for any <a> elements inside a header__social */

.header__social a {
    color: var(--color-primary);
    font-size: 1.75rem;
}

.header__social a:hover {
    transform: scale(1.2);
}


.header_social::after {
    content: '';
    width: 21px;
    height: 2rem;
    background: var(--color-primary);
}

.img-header {
  width: 100%;
  object-fit: cover;
  border: 0.4rem solid var(--color-primary-variant);
  border-radius: 50%;
}



/* ============= ME IMAGE ============= */
.me {
    background: transparent;
    width: 22rem;
    position: relative;
    left: calc(50% - 11rem);
    margin-top: 2rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/* ============= ME IMAGE ============= */
.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* ============= MEDIA QUERIES (MEDIUM DEVICES) ============= */
@media screen and (max-width: 1024px) {
    header {
      height: 68vh;
    }
  }
  
  /* ============= MEDIA QUERIES (SMALL DEVICES) ============= */
  @media screen and (max-width: 600px) {
    header {
        height: 100vh;
      }
  
    .header__social,
    .scroll__down {
      display: none;
    }
  }